import React, { useState, useEffect } from 'react'
import {
  CCard,
  CCardBody,
  CCardHeader,
  CCol,
  CRow,
  CTable,
  CTableBody,
  CTableDataCell,
  CTableHead,
  CTableHeaderCell,
  CTableRow,
  CButton,
  CButtonGroup,
} from '@coreui/react'

import { useHistory } from 'react-router-dom'

import slugify from 'slugify'

import { variable } from '../../configs/variable'

import Loader from '../../components/Loader'
import Message from '../../components/Message'
import { Authentification } from '../../components/index'

import { getUsers, deleteUser } from '../../provider/userFetch'

const Commercants = () => {
  /** State */
  const [users, setUsers] = useState(null)
  const [error, setError] = useState(null)

  /** Hook Route */
  let history = useHistory()

  /** Run once, when the component mounts */
  useEffect(() => {
    console.log('Run once, when the component mounts')

    async function fetch() {
      const resp = await getUsers('commercant')

      resp?.status ? setUsers([...resp?.data]) : setError(resp?.error)
    }

    fetch()
  }, [])

  const deleteFunction = async (id) => {
    const resp = await deleteUser(id)

    if (resp.status) {
      setUsers((old) => old.filter((u) => u._id !== id))
    }
  }

  const handleNavigation = (id) => {
    history.push(`/detail-commercant/${id}`)
  }

  const ajout = () => {
    history.push(`/ajout-commercant`)
  }

  const edit = (id) => {
    history.push(`/ajout-commercant/${id}`)
  }

  if (users === null) {
    return <Loader />
  }

  if (error) {
    return <Message> {error}</Message>
  }

  return (
    <CRow>
      <Authentification />
      <CCol xs={12}>
        <CCard className="mb-4">
          <CCardHeader>
            <CRow>
              <CCol sm={5}>
                <strong>Listes des Commercants</strong>
              </CCol>
              <CCol sm={7}>
                <CButtonGroup className="float-end me-3">
                  <CButton
                    color="outline-secondary"
                    className="mx-0"
                    onClick={() => ajout()}
                  >
                    Ajout
                  </CButton>
                </CButtonGroup>
              </CCol>
            </CRow>
          </CCardHeader>
          <CCardBody>
            <CTable>
              <CTableHead>
                <CTableRow>
                  <CTableHeaderCell scope="col">FirstName</CTableHeaderCell>
                  <CTableHeaderCell scope="col">LastName</CTableHeaderCell>
                  <CTableHeaderCell scope="col">Email</CTableHeaderCell>
                  <CTableHeaderCell scope="col">Restaurant</CTableHeaderCell>
                  <CTableHeaderCell scope="col">Lien</CTableHeaderCell>
                  <CTableHeaderCell scope="col">Actions</CTableHeaderCell>
                </CTableRow>
              </CTableHead>
              <CTableBody>
                {users.length > 0 ? (
                  users.map((user) => (
                    <CTableRow key={user._id}>
                      <CTableDataCell>{user.firstName}</CTableDataCell>
                      <CTableDataCell>{user.lastName}</CTableDataCell>
                      <CTableDataCell>{user.email}</CTableDataCell>
                      <CTableDataCell>
                        {user.restaurants.length
                          ? user.restaurants.map((r) => (
                              <span key={r._id}>
                                {r.name} {' || '}
                              </span>
                            ))
                          : null}
                      </CTableDataCell>
                      <CTableDataCell>
                        <a
                          href={`${variable.endpointProd}`}
                          target="_blank"
                          rel="noreferrer"
                        >
                          {`${variable.endpointProd}/${slugify(user.firstName, {
                            lower: true,
                          })}-${slugify(user.lastName, { lower: true })}`}
                        </a>
                      </CTableDataCell>
                      <CTableDataCell>
                        <CButton
                          color={'primary'}
                          className="me-1"
                          onClick={() => handleNavigation(user._id)}
                        >
                          {' '}
                          Voir{' '}
                        </CButton>
                        <CButton
                          color={'secondary'}
                          className="me-1"
                          onClick={() => edit(user._id)}
                        >
                          {' '}
                          Editer{' '}
                        </CButton>
                        <CButton
                          color={'danger'}
                          onClick={() => deleteFunction(user._id)}
                        >
                          {' '}
                          Supprimer{' '}
                        </CButton>
                      </CTableDataCell>
                    </CTableRow>
                  ))
                ) : (
                  <h3>{'NO Items'}</h3>
                )}
              </CTableBody>
            </CTable>
          </CCardBody>
        </CCard>
      </CCol>
    </CRow>
  )
}

export default Commercants
